.ant-collapse {

    border: none;


    .ant-collapse-content {
        border: none;
        color: white;
        background: transparent;
        letter-spacing: 0.025em;
        font-size: 1.125rem;
    }

    .ant-collapse-item {


        .ant-collapse-header {

            border-radius: 8px 0px 0px 0px;
            display: flex;
            align-items: center;
            font-size: 18px;
            letter-spacing: 0.025em;
            color: #828282;
            background-color: rgba(47, 102, 179, 0.08);

            // border-image: linear-gradient(100deg, rgba(19, 99, 223, 0.60) -200%, transparent) 1;
            // border-top-width: 1px;
            // border-left-width: 1px;
            // border-style: solid;

            &-text {
                font-weight: 600;
            }

            .ant-collapse-expand-icon {
                color: rgba(255, 255, 255, 0.5)
            }
        }

        border: none;


    }

    .ant-collapse-item-active {
        .ant-collapse-header {
            display: flex;
            align-items: center;
            font-size: 18px;
            letter-spacing: 0.025em;
            font-weight: bold;
            color: white;
            background-color: #183351;

            &-text {
                font-weight: bold;
            }

            .ant-collapse-expand-icon {
                color: white
            }
        }

    }
}